		$scope.checkedFun = function(item) {
				if (item.checked) {
						console.log('++' + item.name);
						$scope.checkedCount++;
						item.checked = true;
						$scope.checkedItems.push(item.id)
				} else {
						$scope.checkedCount--;
						console.log('--');
						item.checked = false;
						var findId = $scope.checkedItems.indexOf(item.id);
						$scope.checkedItems.splice(findId, 1);
				};

    };
    
   

